const postUrl = "/api/v1";

export const privacySettingPatchFunction = (builder, method) => {
  return builder.mutation({
    query: (data) => ({
      url: `${postUrl}/extra/${data.id}/privacy/institute/feature`,
      method: `${method}`,
      body: data.privacy,
    }),
  });
};

